import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import './style.css';
import closeIcon from './assets/close.svg'; 
import BoxContainer from './BoxContainer';


const AboutMe = ({ isOpen, onClose }) => {
  const [modalStyle, setModalStyle] = useState({
    overlay: {
      top: 0,
      backgroundColor: 'rgba(255, 255, 255, 0)',
      zIndex: 9999, // Set a high zIndex value
    },
    content: {
      inset: '0px 0px 0px 50vw',
      left: '50vw',
      top: 0,
      border: '0px',
      height: '100vh',
      width: '100%',
      padding: '40px 100px 100px 100px',
      backgroundColor: '#f9f9f9',
      zIndex: 10000, // Set a higher zIndex value
    },
  });

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setModalStyle({
          overlay: {
            top: 0,
            backgroundColor: 'rgba(255, 255, 255, 0)',
            zIndex: 9999, // Set a high zIndex value
          },
          content: {
            inset: '0px',
            left: 0,
            top: 0,
            border: '0px',
            height: '100vh',
            width: '100%',
            padding: '20px 20px 100px 0px',
            backgroundColor: '#f9f9f9',
            zIndex: 10000, // Set a higher zIndex value
          },
        });
      } else {
        setModalStyle({
          overlay: {
            top: 0,
            backgroundColor: 'rgba(255, 255, 255, 0)',
            zIndex: 9999, // Set a high zIndex value
          },
          content: {
            inset: '0px 0px 0px 50vw',
            left: '50vw',
            top: 0,
            border: '0px',
            height: '100vh',

            padding: '40px 100px 100px 100px',
            backgroundColor: '#f9f9f9',
            zIndex: 10000, // Set a higher zIndex value
          },
        });
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <Modal isOpen={isOpen} onClose={onClose} style={modalStyle}>
      <div className="aboutmecontent">
        <img
          src={closeIcon}
          alt="Close icon"
          onClick={onClose}
          className="close-icon"
        />
        <p>ABOUT KIWI</p>
        <div className="detail">
          <p>A UI/UX designer who wants to build great products.<br />Based in Shenzhen.</p>
          <p style={{marginTop: 38 + 'px'}}>
            <b>Design Stack</b>
          </p>
          <p>
            User Experience Design
            <br />
            Interaction Design
            <br />
            Lighting Design
            <br />
            Front-end Dev
          </p>
          <p style={{marginTop: 38 + 'px'}}>
            <b>Awards</b>
          </p>
          <p>Red Dot Design Award 2024<br/>iF Design Award 2023</p>
          <p style={{marginTop: 38 + 'px'}}>
            <b>When I am not designing...</b>
          </p>
          <BoxContainer />
          {/* <p style={{marginTop: 38 + 'px'}}>
            <b>Get In Touch</b>
          </p>
          <a href="mailto:loshochung@gmail.com">
    Email</a> */}
        </div>
      </div>
    </Modal>
  );
};

export default AboutMe;
