import { useNavigate } from 'react-router-dom';

const KiwiButton = ({ to, children, onModalOpen }) => {
  const navigate = useNavigate();

  const handleClick = () => {
    // Handle navigation if `to` prop is provided
    if (to) {
      navigate(to);
      return; // Prevent modal opening if navigation occurs
    }

    // Trigger modal opening logic if `onModalOpen` prop is provided
    if (onModalOpen) {
      onModalOpen();
    }
  };

  return (
    <button onClick={handleClick} className="kiwi-button">
      {children}
    </button>
  );
};

export default KiwiButton;
