import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Layout from './Layout';

import AboutMe from './AboutMe';

import Preloader from './Preloader';
import Projects from './Projects'; // Replace with your Projects component

const App = () => {
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    // Simulate some loading time (replace with your actual loading logic)
    setTimeout(() => {
        setIsLoading(false);
    }, 2500);
}, []);

  return (
    <Router>
    {isLoading ? (
      <Preloader /> // Display preloader while loading
    ) : (
      <Routes>
        <Route path="/" element={<Layout />} /> {/* Layout for all routes */}
        <Route path="about-me" element={<AboutMe />} /> {/* Separate route for AboutMe */}
        {/* Add other routes as needed */}
        <Route path="projects" element={<Projects />} /> {/* Add your Projects route */}
      </Routes>
    )}
  </Router>
  );
};

export default App;
